@tailwind base;
* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}

/* Scrollbar Design */
/* Firefox */
* {
  scrollbar-width: thin;
  scrollbar-color: white black;
}

/* Chrome, Edge, and Safari */
*::-webkit-scrollbar {
  width: 15px;
}

*::-webkit-scrollbar-track {
  background: white;
  border-radius: 5px;
}

*::-webkit-scrollbar-thumb {
  background-color: maroon;
  border-radius: 14px;
  border: 3px solid white;
}
@tailwind components;
@tailwind utilities;

@import url('https://fonts.googleapis.com/css2?family=Inter&display=swap');




/* || VARIABLES */

:root {
  /* COLORS */
  --BGCOLOR: #8d7373;
  --BORDER-COLOR: #FFF;
  --COLOR: rgb(0, 0, 0);
  --ERROR: firebrick;
  --INACTIVE: lightgray;
  --LINK-COLOR: rgb(0, 0, 0);
  --LINK-HOVER: rgba(133, 133, 133, 0.9);
  --NOTE-BGCOLOR: hsla(240, 64%, 27%, 0.65);
  --STATUS-COMPLETE: limegreen;
  --STATUS-OPEN: firebrick;
  --TABLE-BGCOLOR: #FFF;
  --TABLE-COLOR: #000;
  --INPUT-INVALID: #F00;
}

/* || UTILITY CLASSES */
.errmsg {
  display: inline-block;
  background-color: var(--TABLE-BGCOLOR);
  color: var(--ERROR);
  padding: 0.25em;
  margin-bottom: 0.5em;
}

.errmsg a:any-link {
  color: var(--ERROR);
  text-decoration: underline;
}

.icon-button {
  width: 48px;
  height: 48px;
  font-size: 2rem;
  background-color: transparent;
  border: none;
  color: white;
  display: grid;
  place-content: center;
}

.icon-button-black {
  width: 48px;
  height: 48px;
  font-size: 2rem;
  background-color: transparent;
  border: none;
  color: black;
  display: grid;
  place-content: center;
}

.icon-button:hover,
.icon-button:focus-visible {
  transform: scale(1.2);
}

.icon-button[disabled] {
  visibility: hidden;
}

/* || GENERAL STYLES */

body {
  font-family: 'Inter', sans-serif;
  min-height: 100vh;
  color: var(--COLOR);
  background-size: cover;
}
.ba{
  background-image: url('./img/background.jpg');
}

input,
button,
textarea,
select {
  font: inherit;
}

#root {
  /* React root div */
  min-height: 100vh;
  display: flex;
}

a {
  text-decoration: none;
}

/* a:any-link {
  color: var(--LINK-COLOR);
} */

a:hover,
a:focus-visible {
  color: var(--LINK-HOVER);
}

/* || PAGES */

.public,
.welcome {
  display: flex;
  flex-direction: column;
  gap: 1rem;
}

.public {
  padding: 1em;
  flex-grow: 1;
}

.public__main,
.login {
  flex-grow: 1;
  border-top: 2px solid var(--BORDER-COLOR);
  border-bottom: 2px solid var(--BORDER-COLOR);
  padding: 1em 0;
}

.public__addr {
  margin-top: 1em;
}

.welcome a::before {
  content: "➜ "
}

/* || DASH HEADER, CONTAINER & FOOTER */

.dash-header__container--small {
  max-width: 200px;
}

.dash-container {
  padding: 0.75em 0.5em 0.5em;
  flex-grow: 1;
  margin-left: 8%;
}

.dash-footer {
  font-size: 1rem;
  position: sticky;
  bottom: 0;
  color: #FFF;
  z-index: 100;
  background-color: rgb(94,25,27);
  padding: 0.5em;
  display: flex;
  flex-flow: row nowrap;
  justify-content: flex-start;
  gap: 1em;
}

.dash-footer__button {
  font-size: 1rem;
  width: auto;
  height: auto;
}

/* || TABLES - GENERAL */



/* || FORM - GENERAL */

.form {
  display: flex;
  flex-flow: column nowrap;
  gap: 0.75em;
  max-width: 800px;
}

/* .form__checkbox-container {
  display: flex;
  align-items: center;
  width: fit-content;
  gap: 0.5em;
} */

.form__persist {
  display: flex;
  align-items: center;
  width: 100%;
  gap: 0.5em;
}

/* .form__title-row {
  display: flex;
  justify-content: space-between;
  align-items: center;
} */

/* .form__input,
.form__submit-button {
  padding: 0.5em;
}

.form__input--text {
  min-height: 150px;
} */

/* .form__input--incomplete {
  border: 1px solid var(--INPUT-INVALID);
  outline: 1px solid var(--INPUT-INVALID);
} */

.form__action-buttons {
  display: flex;
  justify-content: flex-end;
  align-items: center;
  gap: 0.5em;
  position: absolute;
  right: 0.5em;
}

.form__row {
  display: flex;
  flex-flow: row nowrap;
  gap: 2em;
}

.form__divider {
  display: flex;
  flex-flow: column nowrap;
  gap: 0.5em;
}

@media screen and (max-width: 400px) {
  h1 {
    font-size: 2rem;
  }

  .dash-header__nav {
    gap: 0;
  }

  .icon-button {
    font-size: 1.5rem;
  }
}


@media screen and (min-width: 768px) {

  body {
    font-size: 1.25rem;
  }

  .dash-header {
    padding: 0 1em;
  }

  .dash-container {
    padding: 1em;
  }

  .table {
    font-size: 1.5rem;
  }

  .table--outreach {
    grid-template-columns: repeat(3, 1fr) 2fr repeat(2, 1fr);
  }

  .outreach__created,
  .outreach__updated,
  .outreach__user_id {
    display: block
  }

  .form__action-buttons {
    position: static;
  }
}